@import '../../scss/vars';

.steps-renting {
    margin-bottom: 120px;
    &-title {
        max-width: 711px;
        margin: 0 auto 60px;
        text-align: center;
        font-family: $font-unbounded;
        font-size: 48px;
        font-weight: 600;
        line-height: 62px;
        color: $black-color;
    }
    &__cards {
        display: flex;
        justify-content: space-between;
        
    }
    &__cards-item {
        display: flex;
        justify-content: space-between;
        align-items: flex-start;
        max-width: 424px;
        width: 100%;
        background: $white-color;
        padding: 50px 50px 60px;
        border-radius: 20px;
        &:hover{
            box-shadow: 0px 5px 10px 2px rgba(34, 60, 80, 0.2);
        }
    }
    &__cards-image {
        position: relative;
        display: block;
        &::after {
            content: '1';
            position: absolute;
            display: flex;
            top: -17px;
            right: -14px;
            width: 24px;
            height: 24px;
            background: $gold-color;
            background-position: center;
            background-repeat: no-repeat;
            border-radius: 50%;
            color: $white-color;
            justify-content: center;
            align-items: center;
        }
    }
    &__cards-box {
        max-width: 234px;
    }
    &__cards-box-title {
        margin-bottom: 22px;
        font-size: 20px;
        font-weight: 600;
        line-height: 20px;
        color: $black-color;
    }
    &__cards-box-text {
        font-size: 1rem;
        font-weight: 500;
        line-height: 26px;
        color: $grey-color;
    }
}