@import-normalize;
@import url('https://fonts.googleapis.com/css2?family=Unbounded:wght@200..900&display=swap');
@import './scss/_fonts';
@import './scss/_vars';
@import './scss/mixin';


* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}

html {
    scroll-behavior: smooth;
}
body {
    font-family: 'Montserrat', sans-serif;
    font-size: 14px;
    font-weight: 300;
    background: #F4F4F4;
    color: #222;
    font-family: $font-pangram;
}
img {
    width: 100%;
}
.container {
    max-width: 1860px;
    margin: 0 auto;
}
.wrap-content {
    max-width: 1320px;
    width: 100%;
    margin: 0 auto;
}


@import './scss/media';
