@import '../../scss/vars';

.our-service {
    & .wrap-content{
        padding-right: 40px;
        padding-left: 40px;
    }
    margin-bottom: 120px;
    &-title {
       margin-bottom: 60px;
       font-family: $font-unbounded; 
       font-weight: 700;
       font-size: 48px;
       line-height: 62px;
       color: $black-color;
    }
    &__cards {
        display: flex;
        justify-content: space-between;
        gap: 20px;
    }
    &__cards-item {
        max-width: 424px;
        width: 100%;
        flex: 33%;
    }
    &__cards-img {
        margin-bottom: 42px;
        border-radius: 20px;
        transition: all .5s;
        &:hover{
            transform: scale(1.01);
        }
    }
    &__cards-link {
        display: flex;
        justify-content: space-between;
        gap: 48px;
        
        font-size: 20px;
        font-weight: 700;
        color: $black-color;
        text-decoration: none;
    }
    &__cards-link-button {
        display: block;
        width: 54px;
        height: 54px;
        border: 1px solid $grey-color;
        background: url('../../image/arrow-right.svg');
        background-position: center;
        background-repeat: no-repeat;
        border-radius: 50%;
        margin-right: 60px;
        cursor: pointer;
    }

}