@import '../../scss/vars';

.header {
    position: relative;
    padding: 37px 60px 40px;
    background: $black-color;
    color: $white-color;
    & .container {
        display: flex;
        justify-content: space-between;
        align-items: center;
        align-content: center;
        gap: 10px;
    }
    &__logo img {
        max-width: 200px;
    }
    &__form{
        height: 38px;
        display: flex;
        align-items: center;
        .radio-item {
            margin-right: 1px;
            background: #000;
            height: 100%;
            display: flex;
            align-items: center;
            position: relative;
            border-top-left-radius: 8px;
            border-bottom-left-radius: 8px;
        }
        .radio-item:last-child {
            border-top-left-radius: 0px;
            border-bottom-left-radius: 0px;
            margin-right: 0;
            border-top-right-radius: 8px;
            border-bottom-right-radius: 8px;
        }
    }
    &__form-input {
        z-index: -1;
        opacity: 0;
        position: relative;
    }
    &__form-label svg{
        position: absolute;
        left: 15px;
        top: 50%;
        bottom: 50%;
        transform: translate(-50%, -50%);
        color: $gold-color;
    }
    
    &__form-label{
        cursor: pointer;
        font-size: 12px;
        text-transform: uppercase;
        padding: 8px 14px;
        line-height: 22px;
    }

    &__nav {
        display: flex;
        align-items: center;
        gap: 50px;
        list-style-type: none;
    }
   
    &__nav-link{
        display: inline-block;
        text-decoration: none;
        color: $white-color;
        font-size: 1rem;
        transition: all .3s;
    }

    &__nav-link:hover{
        transform: scale(1.1);
    }

    &__socials {
        display: flex;
        gap: 5px;
    }
    &__socials-link{
        width: 18px;
        height: 18px;
        display: block;
        margin-right: 14px;
        color: rgba($color: #FFF, $alpha: .3);
        & path {
            transition: all, .3s;
        }
        & svg:hover path{
            color: rgba($color: #FFF, $alpha: 1);
            transition: all, .3s;
        }
        &:last-child {
            margin-right: 0;
        }
    }

    &__socials-number{
        display: inline-block;
        color: $white-color;
        font-size: 1.15rem;
        text-transform: uppercase;
        font-weight: 600;
        text-decoration: none;
        font-family: $font-unbounded;
        &:hover {
           text-decoration: underline;
        }
    }
    &__socials-btn{
        cursor: pointer;
        padding: 12px 20px;
        background-color: transparent;
        border: 1px solid $grey-color;
        border-radius: 8px;
        color: $white-color;
        font-weight: 600;
        font-family: $font-pangram;
        transition: all .3s;
    }
    &__socials-btn:hover{
        transform: scale(1.1);
    }
    &__profile{
        display: flex;
        gap: 12px;
    }
    &__profile-button {
        display: flex;
        justify-content: center; 
        align-items: center; 
        width: 46px;
        height: 46px;
        background: #000;
        border-radius: 50%;
        transition: all .3s;
    }
    &__profile-button:hover{
        transform: scale(1.1);
    }
    &__profile-button svg {
        width: auto; 
        height: auto; 
    }
    & .burger-menu {
        display: none;
        background-color: transparent;
        border: none;
        z-index: 101;
        height: 30px;
        &.open .line1 {
            transform: rotate(45deg);
        }
        &.open .line2 {
            opacity: 0;
            transform: translateX(20px);
        }
        &.open .line3 {
            transform: rotate(-45deg);
        }
        &.open {
            position: fixed;
            right: 20px;
        }
    }

    .line {
        display: block;
        height: 5px;
        background-color: #FFF;
        width: 40px;
        margin-bottom: 10px;
        border-radius: 5px;
        position: relative;
        transition: all 0.3s ease-in-out;
        &:last-child{
            margin-bottom: 0px;
        }
    }
    .line1 {
        transform-origin: top left;
      }
      
    .line3 {
        transform-origin: bottom left;
    }
}
.burger-fake {
    opacity: 0;
    width: 40px;
    height: 30px;
    display: none;
}
.sidebar {
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
    overflow-x: hidden;
    position: fixed;
    top: 0;
    right: 0;
    width: 500px;
    height: 100vh;
    z-index: 100;
    background: rgba($color: $black-color, $alpha: .9);
    transition: all .7s;
    transform: translateX(100%);
    &.close {
        transition: all .7s;
        transform: translateX(0);
    }
    &__social{
        display: flex;
        gap: 50px;
        .header__socials-link svg{
            width: 30px;
            height: 30px;
            color: $white-color;
        }
    }
    &__form {
        display: none;
    }
    &__nav{
        display: flex;
        flex-direction: column;
        list-style-type: none;
        align-items: center;
        .header__nav-link{
            font-size: 36px;
            margin-bottom: 30px;
            font-weight: 400;
        }
        
    }
    &__socials-btn {
        font-size: 36px;
        cursor: pointer;
        padding: 12px 20px;
        background-color: transparent;
        border: 2px solid $grey-color;
        border-radius: 8px;
        color: $white-color;
        font-weight: 600;
        font-family: $font-pangram;
        transition: all .3s;

    }

}
// popup order-call
.message-agree {
    position: absolute;
    background-color: #D9D9D9;
    top: 2rem;
    right: 2rem;
    max-width: 26rem;
    padding: 70px;
    z-index: 3;
    &-text {
        text-align: center;
        color: #000;
        font-size: 1.5rem;
    }
}
.order-call {
    position: absolute;
    top: 2rem;
    right: 2rem;
    max-width: 26rem;
    z-index: 3;
    &-wrap {
        position: relative;
        background-color: #D9D9D9;
        padding: 37px 70px 14px 70px;
        border-radius: 10px;
    }
    &__form{
        
    }
    &-close-button {
        position: absolute;
        right: 1.5rem;
        top: 2rem;
        width: 2rem;
        height: 2rem;
        background-image: url(../../image/Close.svg);
        cursor: pointer;
    }
    &__form-title {
        font-size: 1.3rem;
        letter-spacing: 0.3em;
        margin-bottom: 14px;
        color: #000000;
        text-align: center;
    }
    &__form-input {
        width: 100%;
        padding: 13px 80px 8px 20px;
        border-radius: 0.6rem;
        margin-bottom: 38px;
        border: none;
        box-shadow: none;
        min-height: 3.7rem;
        &::placeholder{
            font-size: 1rem;
            color: rgba(#000000, .5);
        }
    }
    &__form-button {
        background: rgba(#477524, .8);
        padding: 0.65rem 3.15rem;
        width: 100%;
        text-align: center;
        border-radius: .65rem;
        border: none;
        margin-bottom: 2rem;
        min-height: 3.7rem;
        font-size: 1rem;
        color: #FFFFFF;
        letter-spacing: 0.3em;
        text-transform: uppercase;
    }
    &__form-checkbox {
        font-size: .8rem;
        color: #000;
    }
}


.order-call__form-label{
    display: flex;
}
.order-call__form-checkbox{
    opacity: 0;
    position: absolute;
}
.fake-checkbox {
    display: block;
    width: 20px;
    height: 20px;
    background: #A4A4A4;
    border-radius: 5px;
    margin-right: 14px;
    position: relative;
    transition: all .3s;
    &::before {
        content: '';
        position: absolute;
        background-image: url(../../image/checkGraysvg.svg);
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        width: 20px;
        height: 20px;
        background-color: white;
        background-repeat: no-repeat;
        background-position: center;
        border-radius: 5px;
        opacity: 0;
    }
}
.order-call__form-checkbox:checked + .fake-checkbox::before {
    opacity: 1;
}
.order-call__form-text {
    max-width: 240px;
    color: #000000;
}
@import '../../scss/media';