@import '../scss/_vars';
@import '../scss/mixin';

@media (max-width: 1920px) {
    .header__nav {
        gap: 30px;
    }
    .hero-bg {
        height: 100%;
    }
    .arenda-wrap {
        padding-right: 60px;
        padding-left: 60px;
    }
    .form-link {
        margin-bottom: 30px;
    }
}
@media (max-width: 1440px) {
    .header{
        padding: 20px 20px;
    }
    .header__nav {
        gap: 20px;
    }
    .header__socials {
        gap: 3px;
        padding-right: 5px;
        padding-left: 5px;
    }
    .header__socials-link {
        margin-right: 5px;
    }
    .header__socials-number {
        font-size: 1rem;
    }
    .hero-text-wrap {
        padding-bottom: 0px;
    }
    .hero-form-wrap {
        margin-top: -80px;
    }
    .catalog__grid-item {
        padding: 66px;
    }
    .promo-wrap {
        gap: 20px;
    }
    .header__socials-number {
        font-size: 0.8rem;
    }
    .header__form-label {
        font-size: 0.6rem;
    }
    
}
@media (max-width: 1310px) {
    .header__nav {
        gap: 10px;
    }
}
@media (max-width: 1280px) {

    .header__socials {
        gap: 1px;
        padding-right: 5px;
        padding-left: 5px;
    }
    
    .hero-form-wrap {
        padding: 45px 50px;
    }
    .hero-form__checkboxes {
        margin-left: 10px;
    }
    .hero-form-data-picker {
        margin-right: 10px;
    }
    .catalog__grid {
        grid-template-columns: repeat(2, 1fr);
        justify-items: center;
    }
    .article {
        padding: 20px;
    }
    .catalog__grid-item {
        padding: 50px;
    }
    .arenda-grid {
        gap: 20px;
    }
    .arenda__info-block {
        max-width: 550px;
    }
}
@media (max-width: 1024px) {
    .hero-bg {
        height: 100%;
    }
    .causes__grid {
        gap: 10px;
    }
    
}

@media (max-width: 1000px) {
    .footer__wrap {
        display: flex;
        flex-direction: column;
        padding: 70px 50px;
    }
    .footer__wrap-info{
        margin-bottom: 50px;
        display: flex;
        flex-direction: column;
    }
    .footer__wrap-nav {
        display: flex;
        justify-content: space-between;
        align-items: center;
        text-align: center;
        max-width: 100%;
        width: 100%;
    }
    .footer__sidebar{
        position: absolute;
        bottom: 0;
        right: 0;
        display: block;
        transform: translateX(100%);
    }
    .footer__socials{
        margin: 30px 0 20px;
        flex-direction: column;
        align-items: flex-start;
    }
    .footer__socials-box{
        margin-bottom: 24px;
    }
    .footer__socials-info{
        align-items: flex-start;
    }
    .footer__socialIcon {
        margin-top: 25px;
        justify-content: flex-start;
    }
    .footer__rights-wrap{
        max-width: 100%;
    }
    .hero-form-wrap {
        padding: 25px 25px;
    }
    
    .hero__list-item {
        @include mobile-text;
    }
    .partners-items {
        flex-wrap: wrap;
        gap: 20px;
    }
    .promo__advantages.promo__advantages--top-border {
        display: grid;
        grid-template-columns: repeat(1, 1fr);
        gap: 20px;
        padding-top: 40px;
    }
    .promo__advantages-item {
        display: grid;
        grid-template-columns: repeat(2, 30% 70%);
        justify-items: center;
    }
}
@media (max-width: 1200px) {
