        display: none;
    }
    .burger-fake.open {
        display: block;
    } 
    .header__socials-btn {
        display: none;
    }
    .header__socials{
        display: none;
    }
    .header__nav {
        display: none;
    }
    .sidebar__nav .header__nav-link  {
        font-size: 2rem;
    }
    .hero-form__checkboxes {
        grid-template-columns: repeat(2, 1fr);
    }
    .hero-form-button {
        padding: 14px 17px;
    }
    .hero-form-data-picker {
        padding: 12px 30px 12px 10px;
    }
    .hero-form label input {
        padding: 0;
        padding-left: 10px;
    }
    .arenda-wrap {
        padding: 90px 60px;
    }
    .arenda__info-block {
        max-width: 500px;
    }
    .arenda-grid {
        gap: 20px;
    }
