    .discounts .discounts__info-title--mobile {
        display: block;
        order: 1;
        margin-bottom: 30px;
        
    }
    .discounts .slider__item-icon {
        top: 22px;
        right: 22px;
    }
    .slider__item-img {
        width: 100%;
        height: 274px;
    }
    .discounts__info-title {
        display: none;
    }
    .discounts__info-text {
        margin-bottom: 20px;
    }
    .discounts .slider__item-wrap {
        padding: 30px;
    }
    .discounts__slider {
        display: flex;
        flex-direction: column;
    }
    .discounts  .slider__item {
        margin-bottom: 0;
    }
    .discounts__slider-nav {
        margin: 30px 0;
    }
    .our-service {
        margin: 20px;  
