    }
    .our-service__cards {
        gap: 20px;
    }
    .our-service__cards-item {
        max-width: 380px;
    }
    .our-service__cards-img {
        width: 100%;
    }
    .our-service__cards-link-button {
        margin-right: 0;
    }
    .causes {
        margin: 20px;
        margin-bottom: 120px;
    }
    .promo-wrap {
        margin: 20px;
        gap: 20px;
    }
    .promo-img{
        max-width: 450px;
        background-size: contain;
        object-fit: contain;
    }
}

