@media (max-width: 650px) {
    .footer__wrap {
        padding: 70px 0 0 0;
        margin: 0;
    }
    .footer__wrap-nav{
        color: #FFF;
        flex-direction: column;
        background-color: $footer-bg;
        padding-bottom: 0px;
        border-bottom: none;
        & figure {
            width: 100%;
            figcaption{
                display: flex;
                align-items: center;
                margin: 0;
                width: 100%;
                background-color:$black-color;
                padding: 20px 25px;
                position: relative;
            }
        }
    }
    .arrow {
        display: block;
        background-image: url('../image/arrow-down.svg');
        width: 12px;
        height: 7px;
        background-repeat: no-repeat;
        position: absolute;
        top: 50%;
        bottom: 50%;
        transform: translate(-50%, -50%);
        right: 30px;
        transition: all .3s;
    }
    .arrow-up {
        transform: rotate(180deg);
    }
    .footer__nav{
        
        max-height: 0px;
