@import '../../scss/vars';
.country-wrap {
    display: flex;
    position: absolute;
    z-index: 3;
    top: 24px;
    right: 24px;
}
.country-wrap--dubai {
    top: 56px;
    right: 24px;
}
.car-park {
    &__head{
        display: flex;
        align-items: center;
        margin-bottom: 48px;
    }
    &__head-title {
        margin-right: 60px;
        font-family: $font-unbounded;
        font-size: 48px;
        font-weight: 600;
    }
    &__head-button{
        padding: 12px 20px;
        font-size: 14px;
        background-color: transparent;
        border: 2px solid $black-color;
        border-radius: 10px;
        max-height: 44px;
        cursor: pointer;
    }
}

.catalog {
    
    &__nav{
        display: flex;
        gap: 20px;
        align-items: center;
        margin-bottom: 50px;
        flex-wrap: wrap;
    }
    &__nav-button {
        cursor: pointer;
        display: flex;
        padding: 27px 30px;
        font-size: 14px;
        font-weight: 600;
        background-color: $white-color;
        border-radius: 10px;
        border: none;
        transition: all .3s;
        &:focus {
            background: #E4E4E4;
        }
    }
    &__nav-img {
        margin-right: 20px;
        color: black;
        transition: all .3s;
        & svg path {
            fill: currentColor;
        }
    }
    &__nav-img.active {
        color: white;
        background: #000;
    }
    &__nav-button:hover {
        color: transparent;
        background: #E4E4E4;
        & svg path {
            fill: #000;
        }
    }
    &__grid {
        display: grid;
        grid-template-columns: repeat(3, 1fr);
        gap: 24px;
    }
    &__grid-item {
        position: relative;
        padding: 66px;
        background-color: $white-color;
        border-radius: 10px;
        transition: all .3s;
        &:hover{
            box-shadow: 0px 5px 10px 2px rgba(34, 60, 80, 0.2);
        }
    }
    &__grid-wrap {
        position: relative;
    }
    &__grid-country {
        padding: 9px 10px;
        font-size: 9px;
        font-weight: 600;
        background-color: #EAEAEA;
        border-radius: 10px;
        text-transform: uppercase;
        &.moscov {
            top: 24px;
            right: 24px;
        }
        &.moscov + .catalog__grid-country--check {
            top: 24px;
            right: 60px;
        }
        &.dubai {
            top: 54px;
            right: 24px;
        }
    }
    &__grid-country--check {
        top: 0;
        right: 0;
        display: inline-block;
        width: 27px;
        height: 27px;
        background: #A2CC5E url('../../image/check.svg');
        background-repeat: no-repeat;
        background-position: center;
        border-radius: 6px;
        margin-right: 6px;
    }
    &__grid-img {
        margin-bottom: 40px;
    }
    &__grid-title {
        margin-bottom: 30px;
        font-size: 20px;
        font-weight: 600;
        color: #000;
        text-transform: uppercase;
    }
    &__grid-box {
        display: flex;
        align-items: center;
        gap: 26px;
        padding-bottom: 34px;
        border-bottom: 1px solid #E4E4E4;
        border-radius: 8px;
        width: 100%;
        margin-bottom: 30px;
    }
    &__grid-check {
        font-size: 13px;
        font-weight: 600;
        color: $grey-color;
        &::before {
            content: url('../../image/engine.svg');
            display: inline-block;
            width: 25px;
            height: 18px;
            margin-right: 15px;
            vertical-align: middle;
        }
    }
    &__grid-weels {
        font-size: 13px;
        font-weight: 600;
        color: $grey-color;
        &::before {
            content: url('../../image/weel.svg');
            display: inline-block;
            width: 25px;
            height: 18px;
            margin-right: 15px;
            vertical-align: middle;
        }
    }
    &__grid-button {
        padding: 10px 20px;
        font-size: 12px;
        color: $white-color;
        font-weight: 600;
        line-height: 20px;
        background: $gold-color;
        border: none;
        border-radius: 6px;
        margin-right: 50px;
        cursor: pointer;
        transition: all .3s;
        &:hover {
            transform: scale(1.1);
        }
    }
    &__grid-price {
        font-size: 14px;
        font-weight: 500;
        line-height: 20px;
        color: #ABABAB;
        & span {
            font-weight: 700px;
            font-size: 1rem;
            color: $black-color;
        }
    }
}