@import '../../scss/vars';
@import '../../scss/mixin';

.causes{
    & .wrap-content{
        padding-right: 40px;
        padding-left: 40px;
    }
    margin-bottom: 120px;
    &-title {
        max-width: 760px;
        width: 100%;
        margin-bottom: 70px;
        @include large-text;
    }
    &__grid {
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        row-gap: 60px;
    } 
    &__grid-img {
        transition: all .3s;
    }
    &__grid-img:hover{
        box-shadow: 0px 5px 10px 2px rgba(34, 60, 80, 0.2);
        border-radius: 35px;
    }
    &__grid-item{
        display: flex;
        justify-content: space-between;
        gap: 40px;
        max-width: 460px;
        width: 100%;
    }
    &__grid-item-box {
        height: 100%;
        max-width: 307px;
        width: 100%;
    }
    &__grid-item-title {
        margin-bottom: 15px;
        font-size: 20px;
        font-weight: 700;
        line-height: 28px;
        color: $black-color;
    }
    &__grid-item-text {
        font-size: 1rem;
        font-weight: 500;
        line-height: 26px;
        color: $grey-color;
    }
}