@font-face {
    font-weight: 600;
    font-family: "PP Pangram Sans Bold";
    src: local("PP Pangram Sans Bold"),
      local("PP Pangram Sans Bold"),
      url("../fonts/PP-Pangram-Sans-Bold-400.woff") format("woff"),
      url("../fonts/PP\ Pangram\ Sans\ Bold\ 400.woff2") format("woff2");
      font-style: normal;
      font-display: swap;
}
@font-face {
    font-weight: 400;
    font-family: "PP-Pangram-Sans-Medium";
    src: local("PP-Pangram-Sans-Medium-400"),
      local("PP-Pangram-Sans-Medium-400"),
      url("../fonts/PP-Pangram-Sans-Medium-400.woff") format("woff"),
      url("../fonts/PP Pangram Sans Medium 400.woff2") format("woff2");
      font-style: normal;
      font-display: swap;
}
