@import '../../scss/vars';

.discounts {
    & .wrap-content{
        padding-right: 40px;
        padding-left: 40px;
    }
    margin-bottom: 120px;
    &-wrap {
        margin: 0 auto;
        padding: 120px 0;
        background: url('../../image/bg-3.jpg');
        & .wrap-content {
            display: flex;
            justify-content: space-between;
            align-items: center
        }
    }
    &__info {
        max-width: 620px;
        width: 100%;
        color: $white-color;
    }
    &__info-title {
        margin-bottom: 50px;
        font-size: 48px;
        font-weight: 600;
        line-height: 62px;
        font-family: $font-unbounded;
        color: $white-color;
        &--mobile{
            display: none;
        }
    }
    &__info-text {
        font-size: 28px;
        font-weight: 500;
        line-height: 38px;
    }
    &__info-description {
        font-size: 18px;
        line-height: 30px;
        font-weight: 400;
        color: $grey-color;
    }
    &__slider {
        max-width: 536px;
        width: 100%;
    }
    & .slider__item {
        margin-bottom: 70px;
        position: relative;
        background: white;
        border-radius: 20px;
    }
    & .slider__item-img{
        border-radius: 20px 20px 0 0;
    }
    & .slider__item-wrap {
        padding: 40px 102px 40px 40px;
    }
    & .slider__item-icon {
        position: absolute;
        top: 55%;
        right: 40px;
        background: url('../../image/flesh.svg');
        background-position: center;
        background-repeat: no-repeat;
        width: 50px;
        height: 50px;
    } 
    & .slider__item-title {
        margin-bottom: 22px;
        font-size: 20px;
        font-weight: 600;
        line-height: 20px;
        color: $black-color;
    }
    & .slider__item-text {
        max-width: 384px;
        font-size: 1rem;
        font-weight: 400;
        line-height: 26px;
        color: $grey-color;
    }
    &__slider-nav {
        display: flex;
        align-items: center;
        .slider-btn {
            margin-right: 14px;
            height: 54px;
            width: 54px;
            border-radius: 50%;
            background-color: transparent ;
            border: 2px solid $grey-color;
            cursor: pointer;
            transition: all .3s;
            &:last-child{
                margin-right: 64px;
            }
            &:hover{
                background-color: $grey-color;
            }
        }
        & .slider-prev {
            background:transparent url('../../image/white-arrow-left.svg') ;
            background-position: center;
            background-repeat: no-repeat;
            
        }
        & .slider-next {
            background:transparent  url('../../image/white-arrow-right.svg');
            background-position: center;
            background-repeat: no-repeat;
        }
        & .slider-pages {
            display: flex;
            align-items: center;
            font-size: 20px;
            font-weight: 600;
            line-height: 22px;
            color: $grey-color;
            margin-left: 64px;
        }
        .active {
            color: white;
            font-size: 26px;
            line-height: 40px;
        }
    }
    
}

// slider 
.slider__items {
    position: relative;
    width: 100%; 
    overflow: hidden; 
}

.slider__item {
    width: 100%; 
    box-sizing: border-box; 
}