@import '../../scss/vars';

.suply {
    margin-bottom: 120px;
    &-title {
        margin-bottom: 60px;
        font-family: $font-unbounded;
        font-weight: 600;
        font-size: 48px;
        color: $black-color;
        line-height: 62px;
    }
    &__show-items {
        display: flex;
        justify-content: space-between;
    }
    &__show-item {
        max-width: 648px;
        width: 100%;
        background: white;
        border-radius: 20px 20px 20px 20px;
        // 
        

    }
    &__show-item-img {
        object-fit: cover;
        border-radius: 20px 20px 0 0;
    }
    &__show-item-wrap {
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding: 50px;
    }
    &__show-item-info-box {
        max-width: 342px;
    }
    &__show-item-title {
        margin-bottom: 22px;
        font-size: 20px;
        font-weight: 600;
        color: $black-color;
    }
    &__show-item-text {
        font-size: 16px;
        font-weight: 500;
        color: $grey-color;
    }
    &__show-item-btn {
        background: url('../../image/arrow-right.svg');
        background-repeat: no-repeat;
        background-position: center;
        height: 54px;
        width: 54px;
        border-radius: 50%;
        border: 1px solid #EBEBEB;
        cursor: pointer;
        &:hover {
            background-color: #F4F4F4;
        }
    }
}

.suply__show-items {
    display: flex;
    gap: 20px;
    perspective: 1000px;
  
    .suply__show-item {
      position: relative;
      width: 100%;
      transition: transform 0.3s ease, box-shadow 0.3s ease;
      transform-style: preserve-3d;
  
      &:hover {
        transform: rotateY(1deg) rotateX(-1deg) scale(1.001);
        box-shadow: 0 20px 40px rgba(0, 0, 0, 0.2);
      }
    }
  }