@import '../../scss/vars';
.footer{
    bottom: 0;
    right: 0;
    left: 0;
    background: $color-bg;
    &__wrap {
        max-width: 1860px;
        background-color: $footer-bg;
        border-top-left-radius: 20px;
        border-top-right-radius: 20px;
        padding: 100px 50px;
        display: grid;
        grid-template-columns: 1fr minmax(10px, 100px) 1fr;
        grid-template-rows: auto 1fr;
    }
    &__wrap-info{
        grid-column: 1 / 2;
        grid-row: 1 / 3;
        margin: 0 auto;  
        max-width: 400px;
    }
    &-logo img{
        width: 381px;
    }
    &__socials {
        display: flex;
        margin-top: 60px;
        margin-bottom: 40px;
    }

    &__socials-box{
        margin-right: 30px;
        text-align: center;
    }
    &__socials-number {
        display: block;
        color: $white-color;
        text-decoration: none;
        font-size: 18px;
        font-weight: 600;
        margin-bottom: 10px;
        font-family: $font-unbounded;
    }
    &__socials-text{
        color: $no-active;
        display: block;
        font-size: 1rem;
        font-weight: 400;
    }
    &__socials-button{
        font-family: $font-pangram;
        cursor: pointer;
        padding: 12px 20px;
        color: $white-color;
        background-color: transparent;
        border: 1px solid $white-color;
        border-radius: 8px;
    }
    &__socials-info {
        display: flex;
        flex-direction: column;
        align-items: flex-end;
        color: $no-active;
    }
    &__socials-email{
        margin-top: 8px;
        text-decoration: none;
        color: $white-color;
    }
    &__socialIcon{
        margin-top: 36px;
        display: flex;
        justify-content: flex-end;
       
    }
    
    &__social-link{
        margin-right: 14px;
        color: rgba($color: #FFF, $alpha: .3);
        & path {
            transition: all, .3s;
        }
        & svg:hover path{
            color: rgba($color: #FFF, $alpha: 1);
            transition: all, .3s;
        }
    }
    &__social-link:last-child{
        margin-right: 0;
    }
    &__wrap-nav{
        grid-column: 3 / 4; 
        grid-row: 1 / 2;  
        display: flex;
        justify-content: space-between;
        max-width: 600px;
        padding-bottom: 46px;
        border-bottom: 2px solid rgba($color: #FFF, $alpha: .14);
        margin-bottom: 46px;
        & figcaption {
            color: #FFF;
        }
    }
    &__wrap-nav figcaption{
        font-size: 1rem;
        font-weight: 600;
        font-family: $font-pangram;
        margin-bottom: 20px;
        text-transform: uppercase;
    }
    &__nav{
        list-style-type: none;
        font-size: 1rem;
    }
    &__nav-link{
        font-family:  $font-pangram;
        margin-bottom: 15px;
        transition: all .3s;
    }
    &__nav-link:hover{
        transform: scale(1.1);
    }
    &__nav-link a{
        text-decoration: none;
        color: $no-active;
    }
    &__rights-wrap{
        max-width: 600px;
        grid-column: 3 / 4; 
        grid-row: 2 / 3;
        display: flex;
        justify-content: space-between; 
    }
    &__rights-right{
        text-decoration: none;
        color: $grey-color;
    }
    &__sidebar{
        display: none;
    }
}

@import '../../scss/media';