@import '../../scss/_vars';

.arenda {
    margin-bottom: 120px;
    &-wrap{
        padding: 120px 270px;
        background-image: url('../../image/bg-2.jpg');
        background-repeat: no-repeat;
        border-radius: 12px;
    }
    &-title {
        margin-bottom: 70px;
        font-size: 48px;
        color: $white-color;
        font-weight: 600;
        font-family: $font-unbounded;
    }
    &-grid {
        display: flex;
        justify-content: space-between;
    }
    &__info-block {
        max-width: 760px;
    }
    &__info-text {
        margin-bottom: 36px;
        font-size: 28px;
        font-weight: 500;
        color: $white-color;
    }
    &__info-description {
        font-size: 18px;
        font-weight: 400;
        color: #9F9F9F;
    }
    &__form {
        position: relative;
        max-width: 424px;
        width: 100%;
    }
    &__form-input {
        display: block;
        margin-bottom: 14px;
        width: 100%;
        padding: 28px 30px;
        border-radius: 10px;
        color: $black-color;
        &::placeholder {
            font-size: 1rem;
            font-weight: 500;
            color: #A4A4A4;
        }
        
    }
    &__form-btn {
        width: 100%;
        padding: 28px 34px;
        margin-bottom: 40px;
        background: transparent;
        border: 2px solid white;
        color: white;
        border-radius: 10px;
        cursor: pointer;
    }
    &__form-paragraph {
        max-width: 321px;
        font-weight: 500;
        font-size: 14px;
        line-height: 20px;
        color: #A4A4A4;
        display: inline-block;
    }
    &__form-link {
        color: white;
        text-decoration: none;
    }
}

.arenda__form-label{
    display: flex;
    position: relative;
}
.arenda__form-checkbox{
    opacity: 0;
    position: absolute;
}
.fake-checkbox {
    display: inline-block;
    width: 20px;
    height: 20px;
    background: #A4A4A4;
    border-radius: 5px;
    margin-right: 14px;
    position: relative;
    transition: all .3s;
    cursor: pointer;
    &::before {
        content: '';
        position: absolute;
        background-image: url(../../image/checkGraysvg.svg);
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        width: 20px;
        height: 20px;
        background-color: white;
        background-repeat: no-repeat;
        background-position: center;
        border-radius: 5px;
        opacity: 0;
    }
}
.arenda__form-checkbox:checked + .fake-checkbox::before {
    opacity: 1;
}

.arenda__form-error {
    color: red;
    margin-bottom: 20px;
    font-size: 1rem;
    &:last-child{
        margin-bottom: 0;
        position: absolute;
        bottom: -40px;
    }
}