@import '../../scss/vars';

.hero {
    padding-bottom: 120px;
    background-color: #F4F4F4;
    &-wrap {
        display: flex;
        flex-direction: column;
        align-items: center;
        margin: 0 auto;
        position: relative;
    }
    &-bg {
        position: absolute;
        z-index: 1;
        max-width: 1860px;
        width: 100%;
        height: auto;
        margin: 0 auto;
        border-radius: 0 0 32px 32px;
        object-fit: cover;
        &::after {
            content: '';
            position: absolute;
            top: 0;
            right: 0;
            left: 0;
            height: 100%;
            width: 1920px;
            background-color: #000;
            z-index: 1;
        }
    }
    &-text-wrap {
        display: flex;
        flex-direction: column;
        align-items: center;
        width: 100%;
        max-width: 1320px;
        position: relative;
        margin: 0 auto;
        z-index: 2;
        padding: 170px 0 120px;
        text-align: center;
    }
    &-title {
        font-family: $font-unbounded;
        font-size: 89px;
        font-weight: 700;
        line-height: 95px;
        color: $white-color;
        margin-bottom: 40px;

    }
    &-subtitle {
        width: 100%;
        font-size: 24px;
        font-weight: 500;
        line-height: 32px;
        color: $white-color;
        padding-bottom: 120px;
        border-bottom: 2px solid rgba($color: #FFF, $alpha: .2);
        margin-bottom: 70px;
    }
    &__list {
        max-width: 1200px;
        display: flex;
        justify-content: space-between;
        color: $white-color;
        margin-bottom: 125px;
    }
    &__list-item{
        display: flex;
        font-size: 18px;
        line-height: 26px;
        font-weight: 500;
        list-style: none;
        text-align: left;
    }
}

.item-1::before{
    content: '';
    background-image: url('../../image/1.svg');
    background-repeat: no-repeat;
    width: 32px;
    height: 32px;
    display: block;
    margin-right: 20px;
    
}
