.hero-form-wrap {
    position: relative;
    width: 100%;
    max-width: 1320px;
    height: 100%;
    max-height: 271px;
    background-color: #FFF;
    z-index: 3;
    padding: 60px 70px;
    margin: 0 auto;
    border-radius: 20px;
}
.hero-form-title {
    font-size: 22px;
    line-height: 29px;
    font-family: $font-unbounded;
    color: $black-color;
    margin-bottom: 36px;
}

.hero-form {
    display: flex;
    justify-content: space-between;
    align-items: center;
    &-date {
        display: flex;
    }
    &-data-picker {
        position: relative;
        padding: 18px 42px 18px 20px;
        background: #F4F4F4;
        border-radius: 10px;
        margin-right: 20px;
        &:last-child {
            margin-right: 0;
        }
    }
    & label{
        display: flex;
        vertical-align: middle;
        & input {
            background: transparent;
            border: none;
            padding-left: 16px;
            padding: 10px;
            padding-left: 5px;
            margin-left: 5px;
            &::placeholder{
                color: #A4A4A4;
                font-size: 14px;
            }
            &:focus{
            outline: 1px solid #9F9F9F;
            }
        }
        & img {
            width: 14px;
        }
    }
    &__checkboxes {
        position: relative;
        display: grid;
        grid-template-columns: repeat(3, 1fr);
        column-gap: 13px;
        row-gap: 14px;
        margin-left: 20px;
    }
    &__label{
        position: relative;
    }
    &__input {
        opacity: 0;
        position: absolute;
    }
    & .fake-checkbox {
        display: inline-block;
        width: 20px;
        height: 20px;
        background: #EEEEEE;
        border-radius: 5px;
        margin-right: 14px;
        position: relative;
        transition: all .3s;
        &::before {
            content: '';
            position: absolute;
            background-image: url(../../image/checkbox.svg);
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);
            width: 20px;
            height: 20px;
            background-color: #000;
            background-repeat: no-repeat;
            background-position: center;
            border-radius: 5px;
            opacity: 0;
        }
    }
    &__input:checked + .fake-checkbox::before {
        opacity: 1;
    }

    &-button {
        padding: 28px 34px;
        color: $white-color;
        font-size: 1rem;
        line-height: 20px;
        font-weight: 700;
        background: $gold-color;
        border: none;
        border-radius: 10px;
        cursor: pointer;
        transition: all .3s;
        &:hover {
            transform: scale(1.1);
        }
    }
}

.hero-form-error {
    position: absolute;
    bottom: -22px;
    color: red;
}
.hero-form-error-checkbox {
    position: absolute;
    bottom: -26px;
    color: red;
}

.hero-form-data-picker input[type="date"]::-webkit-calendar-picker-indicator {
    position: absolute;
    opacity: 0;
    width: 100%;
}