@import '../../scss/vars';
@import '../../scss/mixin';

.form {
    max-width: 670px;
    width: 100%;
    &-wrap .wrap-content {
        display: flex;
        justify-content: space-between;
        gap: 50px
    }
    &-title {
        @include large-text;
        margin-bottom: 40px;
        color: $black-color;
    }
    &-text {
        margin-bottom: 30px;
        font-size: 18px;
        font-weight: 500;
        line-height: 30px;
        color: $grey-color;
    }
    &-link {
        display: block;
        margin-bottom: 60px;
        font-size: 20px;
        font-weight: 700;
        line-height: 24px;
        font-family: $font-unbounded;
        color: $black-color;
        text-decoration: none;
    }
    &-input {
        display: block;
        margin-bottom: 14px;
        padding: 28px 30px;
        max-width: 480px;
        width: 100%;
        border-radius: 10px;
        background: $white-color;
        color: $black-color;
        border: none;
        
        &::placeholder {
            font-size: 1rem;
            font-weight: 500;
            line-height: 20;
            color: $black-color;
            opacity: 1;
        }
        &--tel::placeholder {
            color: $grey-color;
        }
        
    }
    &-submit {
        display: block;
        padding: 28px;
        margin-bottom: 40px;
        max-width: 480px;
        width: 100%;
        border-radius: 10px;
        background-color: $black-color;
        color: $white-color;
        font-size: 1rem;
        line-height: 20px;
        font-weight: 700;
        cursor: pointer;
    }
    &-label {
        position: relative;
        display: flex;
        max-width: 380px;
        width: 100%;
        font-size: 14px;
        line-height: 20px;
        font-weight: 500;
        color: $grey-color;
        &-link {
            color: $black-color;
            text-decoration: none;
            cursor: pointer;
        }
    }
    &-box {
        max-width: 343px;
        cursor: pointer;
    }
}
.form-checkbox {
    position: absolute;
    top: 0;
    left: 0;
    width: 20px;
    height: 20px;
    opacity: 0;
    z-index: 1;
    cursor: pointer;
}

.form-checkbox + .form-fake-checkbox {
    display: block;
    width: 22px;
    height: 20px;
    background: $white-color;
    border-radius: 5px;
    margin-right: 14px;
    position: relative;
    cursor: pointer;
    transition: all .3s;
}

.form-checkbox + .form-fake-checkbox::before {
    content: '';
    position: absolute;
    width: 12px;
    height: 12px;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    background-image: url('../../image/checkGraysvg.svg'); 
    background-repeat: no-repeat;
    background-position: center;
    background-size: contain;
    opacity: 0;
    transition: opacity .3s;
}

.form-checkbox:checked + .form-fake-checkbox::before {
    opacity: 1;
}

.form-error {
    color: red;
    margin-bottom: 20px;
    font-size: 1rem;
    &:last-child{
        margin-bottom: 0;
        margin-top: 10px;
    }
}