@import '../../scss/vars';
@import '../../scss/mixin';

.promo {
    &-bg {
        padding-top: 120px; 
        padding-bottom: 120px; 
        background: url('../../image/bg-promo.jpg');
        border-radius: 20px 20px 0 0;
        background-repeat: no-repeat;
        background-position: center;
        background-size: cover;
        padding-right: 40px;
        padding-left: 40px;
    }
    &-wrap {
        display: flex;
        justify-content: space-between;
        gap: 50px;
        margin-bottom: 110px;
    }
    &-img{
        width: 100%;
        border-radius: 20px;
    }
    &__info-box{
        max-width: 648px;
    }
    &-title--mobile {
        display: none;
    }
    &-title {
        @include large-text;
        color: $white-color;
        margin-bottom: 50px;
    }
    &-text {
        margin-bottom: 36px;
        font-size: 28px;
        font-weight: 600;
        line-height: 38px;
        color: $white-color;
        &--bold{
            color: $white-color;
            font-size: 20px;
        }
    }
    &-small-text{
        font-size: 18px;
        line-height: 30px;
        font-weight: 500;
        color: $grey-color;
        &--sub-text {
            margin-top: 36px;
            max-width: 550px;
        }
        &--right {
            max-width: 550px;
        }
    }
    &__advantages {
        display: flex;
        justify-content: space-between;
        color: $white-color;
        &--top-border {
            padding-top: 68px;
            margin-top: 50px;
            border-top: 1px solid $grey-color;
        }
    }
    &__advantages-item {
        display: flex;
        margin-right: 29px;
        font-size: 13px;
        font-weight: 700;
        line-height: 24px;
        color: $white-color;
        &:last-child {
            margin-right: 0;
        }
        &--8::before {
            content: url('../../image/8.svg');
            width: 21px;
            height: 21px;
            margin-right: 18px;
        }
        &--24::before {
            content: url('../../image/24.svg');
            width: 49px;
            height: 21px;
            margin-right: 18px;
        }
        &--car::before {
            content: url('../../image/car-black.svg');
            width: 29px;
            height: 20px;
            margin-right: 18px;
        }
    }
}



